.header-container{
    position:sticky;
    top:0px;
    left:0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-width:1920px;
    /* border:4px solid red; */
    box-sizing: border-box;
    height:80px;
    padding:0px 290px;
    background-color: rgba(240, 240, 240, 0.596);
}
.logo{
 color:blue;
 font-size:36px;
}
.menu-container{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.menu{
    cursor: pointer;
    height:100%;
    font-size:16px;
    height: 56px;
    padding:20px 10px 10px;
    box-sizing: border-box;
    margin-right:40px;
}
.menu:last-child{
    margin:0px;
}
